<template>
  <div class="makeInvoice">
    <dth-header></dth-header>
    <div class="doInterval-action" v-show="isDoInterval">
      <span class="num">{{ backNum }}</span>秒后自动跳转到首页...
    </div>
    <dth-back-top></dth-back-top>
    <dth-back-home></dth-back-home>
    <dth-flow-node>
       <li>
          <div class="flow-node0 overIdCard item"></div>
          <span class="flow-text">身份证识别</span>
      </li>
      <li class="diliver over"></li>
      <li>
          <div  class="flow-node1 overCompareAuth item"></div>
         <span class="flow-text">人证对比</span>
      </li>
      <li class="diliver over"></li>
      <li>
          <div class="flow-node2 overInvoice item"></div>
          <span class="flow-text">开票二维码识别</span>
      </li>
      <li class="diliver over"></li>
      <li>
          <div class="flow-node3 print item"></div>
          <span class="flow-text">发票打印</span>
      </li>
    </dth-flow-node>
    <div class="makeInvoice-body">
      <h5>{{printStatusMsg}}</h5>
      <div class="card-body">
              <img :src="kaipiaoUrl" class="kaipiaoImg" />
              <div v-show="isContent" class="card-status-content">
                <span class="readContent">{{cardReadStatus}}</span>
              </div>
      </div>
      <el-form
      v-show="false"
        class="invoiceForm"
        ref="makeInvoiceForm"
        label-width="185px"
        autocomplete="on"
        label-position="right"
        :model="makeInvoiceForm"
        :rules="makeInvoiceFormRules"
      >
        <el-row>
          <el-col :span="12">
            <el-form-item label="买方单位/个人住址">
              <el-input
                v-model="makeInvoiceForm.buyerAddress"
                ref="buyerAddress"
                placeholder="买方单位/个人"
                name="buyerAddress"
                tabindex="1"
                disabled
                autocomplete="on"
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="买方单位代码/身份证号码">
              <el-input
                v-model="makeInvoiceForm.buyerIdCard"
                ref="buyerIdCard"
                placeholder="单位代码/身份证号码"
                name="buyerIdCard"
                tabindex="1"
                disabled
                autocomplete="on"
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="买方单位/个人名称">
              <el-input
                v-model="makeInvoiceForm.buyerName"
                ref="buyerName"
                placeholder="买方单位/个人住址"
                name="buyerName"
                tabindex="1"
                disabled
                autocomplete="on"
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="电话">
              <el-input
                v-model="makeInvoiceForm.buyerTelephone"
                ref="buyerTelephone"
                placeholder="电话"
                name="buyerTelephone"
                tabindex="1"
                disabled
                autocomplete="on"
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="卖方单位/个人住址">
              <el-input
                v-model="makeInvoiceForm.sellerAddress"
                ref="sellerAddress"
                placeholder="卖方单位/个人"
                name="sellerAddress"
                tabindex="1"
                disabled
                autocomplete="on"
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="卖方单位代码/身份证号码">
              <el-input
                v-model="makeInvoiceForm.sellerIdCard"
                ref="sellerIdCard"
                placeholder="卖方单位代码/身份证号码"
                name="sellerIdCard"
                tabindex="1"
                disabled
                autocomplete="on"
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="卖方单位/个人名称">
              <el-input
                v-model="makeInvoiceForm.sellerName"
                ref="sellerName"
                placeholder="卖方单位/个人住址"
                name="sellerName"
                tabindex="1"
                disabled
                autocomplete="on"
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="电话">
              <el-input
                v-model="makeInvoiceForm.sellerTelephone"
                ref="sellerTelephone"
                placeholder
                name="sellerTelephone"
                tabindex="1"
                disabled
                autocomplete="on"
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="8">
            <el-form-item label="车牌号码">
              <el-input
                v-model="makeInvoiceForm.vehicleNumber"
                ref="vehicleNumber"
                placeholder="车牌号码"
                name="vehicleNumber"
                tabindex="1"
                disabled
                autocomplete="on"
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="登记证号">
              <el-input
                v-model="makeInvoiceForm.vehicleCertificateCode"
                ref="vehicleCertificateCode"
                placeholder="登记证号"
                name="vehicleCertificateCode"
                tabindex="1"
                disabled
                autocomplete="on"
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="车辆类型">
              <el-input
                v-model="makeInvoiceForm.vehicleType"
                ref="vehicleType"
                placeholder="车辆类型"
                name="vehicleType"
                tabindex="1"
                disabled
                autocomplete="on"
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="8">
            <el-form-item label="车架号/识别码">
              <el-input
                v-model="makeInvoiceForm.vehicleIdentificationNumberCertificate"
                ref="vehicleIdentificationNumberCertificate"
                placeholder="车架号/车辆识别代码"
                name="vehicleIdentificationNumberCertificate"
                tabindex="1"
                disabled
                autocomplete="on"
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="厂牌类型">
              <el-input
                v-model="makeInvoiceForm.carLabelType"
                ref="carLabelType"
                placeholder="厂牌型号"
                name="carLabelType"
                tabindex="1"
                disabled
                autocomplete="on"
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="转入地车辆管理所名称">
              <el-input
                v-model="makeInvoiceForm.vehicleManagement"
                ref="vehicleManagement"
                placeholder="转入地车辆管理所名称"
                name="vehicleManagement"
                tabindex="1"
                disabled
                autocomplete="on"
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="车辆价格(大写)">
              <el-input
                v-model="makeInvoiceForm.vehiclePrice"
                ref="vehiclePrice"
                placeholder="车辆价格(大写)"
                name="vehiclePrice"
                tabindex="1"
                disabled
                autocomplete="on"
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="小写">
              <el-input
                v-model="makeInvoiceForm.totalPriceLower"
                ref="totalPriceLower"
                placeholder="小写"
                name="totalPriceLower"
                tabindex="1"
                disabled
                autocomplete="on"
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="单位名称">
              <el-input
                v-model="makeInvoiceForm.organizationName"
                ref="organizationName"
                placeholder="单位名称"
                name="organizationName"
                tabindex="1"
                disabled
                autocomplete="on"
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="单位类型">
              <el-select
                ref="trNode"
                disabled
                v-model="makeInvoiceForm.organizationType"
                @change="((val) => { organizationAction(val) })"
                placeholder="单位类型"
              >
                <el-option label="二手交易市场" :value="0"></el-option>
                <el-option label="拍卖单位" :value="1"></el-option>
                <el-option label="经营单位" :value="2"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="单位地址">
              <el-input
                v-model="makeInvoiceForm.organizationAddress"
                ref="organizationAddress"
                placeholder
                name="organizationAddress"
                tabindex="1"
                disabled
                autocomplete="on"
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="纳税人识别号">
              <el-input
                v-model="makeInvoiceForm.payerIdentify"
                ref="payerIdentify"
                placeholder="纳税人识别号"
                name="payerIdentify"
                tabindex="1"
                disabled
                autocomplete="on"
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="开户银行、帐号">
              <el-input
                v-model="makeInvoiceForm.bankAccount"
                ref="bankAccount"
                placeholder="开户银行、帐号"
                name="bankAccount"
                tabindex="1"
                disabled
                autocomplete="on"
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="电话">
              <el-input
                v-model="makeInvoiceForm.organizationTelephone"
                ref="organizationTelephone"
                placeholder="电话"
                name="organizationTelephone"
                tabindex="1"
                disabled
                autocomplete="on"
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="备注">
              <el-input
                v-model="makeInvoiceForm.remark"
                ref="remark"
                placeholder="备注"
                name="remark"
                tabindex="1"
                disabled
                autocomplete="on"
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="开票人">
              <el-input
                v-show="!isSignatureImg"
                v-model="makeInvoiceForm.drawer"
                ref="drawer"
                placeholder="开票人"
                name="drawer"
                tabindex="1"
                disabled
                autocomplete="on"
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      
    </div>
    <div class="completed-body">
        <div class="printBtn dyfp-btn" v-show="isDybtn" @click="printAction">打印发票</div>
        <div class="printBtn undyfp-btn" v-show="isRepeatDybtn" @click="repeatDyInvoice">重新尝试打印发票</div>
    </div>
    <audio v-show="aVisible" controls="controls" id="printVoince">
        <source src="../assets/printVoince.mp3" type="audio/mpeg">
    </audio>
    <dth-footer></dth-footer>
  </div>
</template>
<script>
import { Message } from "element-ui";
import $ from 'jquery';
import { getOpFlow, postPretrialInfoSave, postfindStateInfo, getPretrialInfoFind, doKPXTWebService } from '@/api/user'
import { numTranslateToUpperCN, setStorage, getStorage, doInterval } from "@/utils/auth";
import DthHeader from '@/components/DthHeader/DthHeader.vue';
import DthFooter from '@/components/DthFooter/DthFooter.vue';
import DthBackHome from '@/components/DthBackHome/DthBackHome.vue'
import DthBackTop from '@/components/DthBackTop/DthBackTop.vue'
import DthFlowNode from '@/components/DthFlowNode/DthFlowNode.vue'
export default {
  name: "PrintInvoice",
  components: { DthHeader, DthFooter, DthBackHome, DthBackTop, DthFlowNode },
  data() {
    return {
      dyTitle: '打印提示',
      dyVisible: false,
      aVisible: false,
      isSignatureImg: false,
      printStatusMsg: '开始打印发票',
      kaipiaoUrl: require('../assets/print0.png'),
      isContent: true,
      makeInvoiceForm: {
        buyerAddress: "",
        buyerIdCard: "",
        buyerName: "",
        buyerTelephone: "",
        sellerAddress: "",
        sellerIdCard: "",
        sellerName: "",
        sellerTelephone: "",
        vehicleNumber: "",
        registerNumber: "",
        vehicleType: "",
        vehicleIdentificationNumberCertificate: "",
        carLabelType: "",
        vehicleManagement: "",
        vehiclePrice: "",
        totalPriceLower: "",
        organizationName: "",
        organizationType: "",
        organizationAddress: "",
        payerIdentify: "",
        bankAccount: "",
        organizationTelephone: "",
        remark: "",
        drawer: ""
      },
      makeInvoiceFormRules: {},
      cardReadStatus: '',
      isStatusChanged: false,
      isDybtn: false,
      isRepeatDybtn: false,
      repeatReadCard: false,
      backNum: 180,
      isDoInterval: false,
      loopTimer: null,
      doIntervalTimer: null,
    };
  },
  mounted() {
    console.log($);
    this.$nextTick(() => {
      const element = document.documentElement;
      if (element.requestFullscreen) {
        element.requestFullscreen();
      } else if (element.mozRequestFullScreen) {
        element.mozRequestFullScreen();
      } else if (element.webkitRequestFullscreen) {
        element.webkitRequestFullscreen();
      } else if (element.msRequestFullscreen) {
        element.msRequestFullscreen();
      }
    });
    this.doInvoiceInfo();
    document.addEventListener('keypress', this.resetTimer, false);
    document.addEventListener('keyup', this.resetTimer, false);
    document.addEventListener('keydown', this.resetTimer, false);
    document.addEventListener('click', this.resetTimer, false);
    clearInterval(this.loopTimer)
    this.loopTimer = setInterval(() => {
        // this.doInterval('doInvoice')
    }, 1000)
  },
  destroyed () {
    clearInterval(this.loopTimer);
  },
  methods: {
    resetTimer() {
        this.backNum = 180;
        clearInterval(this.loopTimer)
        this.loopTimer = setInterval(() => {
            //this.doInterval('doInvoice')
        }, 1000)
    },
    doInterval: doInterval,
    backHome() {
        this.$router.push({path: '/doInvoice'})
    },
    dyClose() {
      console.log(1)
        this.dyVisible = false;
    },
    printAction() {
        $('.card-status-content').empty().html(`
        <div class="print-body">
          <div class="title">打印提示</div>
          <div class="dy-body">
              每份发票只能打印一次，
              是否确认要打印？
          </div>
          <div class="dy-confirm-body">
              <div class="dybtn dybtn-cancle" @click="dyClose">取消</div>
              <div class="dybtn dybtn-make" @click="onSubmit">确认</div>
          </div>
        </div>
        `);

        this.$nextTick(() => {
          $('.dybtn-cancle').on('click', () => {
            location.reload()
          })

          $('.dybtn-make').on('click', () => {
            this.onSubmit();
          })
        })
    },
    repeatDyInvoice() {
        location.reload(true);
    },
    doInvoiceInfo() {
      // const { objId } = JSON.parse(sessionStorage.getItem('invoiceData'))
      
      const invoiceId = getStorage('invoiceId');
      getPretrialInfoFind(invoiceId).then(resp => {
        
        this.cardReadStatus = '发票显示成功！';
        this.isStatusChanged = false;
        this.isDybtn = true;
        this.isRepeatDybtn = false;
        for (const key in resp.data) {
          if (key === "vehiclePrice") {
            this.makeInvoiceForm[key] = numTranslateToUpperCN(resp.data[key]/100);
          } else {
            this.makeInvoiceForm[key] = resp.data[key];
          }
        }
        
        document.getElementById('printVoince').play();
        setStorage('invoiceData', resp.data)
      }).catch(error => {
        this.cardReadStatus = '发票显示失败，请重新打印发票！';
        this.isDybtn = false;
        this.isRepeatDybtn = true;
        this.isStatusChanged = true;
        console.dir('CustomError' + JSON.stringify(error));
      });
    },
    onSubmit() {
      // this.dyVisible = false;
      this.isDybtn = false;
      $('.card-status-content').empty().html(`
        <div class="printing">
          打印中
        </div>
      `);
      
      const { flowId } = getStorage("invoiceData");
      const invoiceId = getStorage("invoiceId");

      /** 开具发票 */
      const {
        buyerName,
        buyerIdCard,
        buyerAddress,
        buyerTelephone,
        sellerName,
        sellerAddress,
        sellerIdCard,
        sellerTelephone,
        vehicleNumber,
        vehicleCertificateCode,
        carLabelType,
        vehicleIdentificationNumberCertificate,
        vehicleType,
        vehiclePrice
      } = getStorage("invoiceData");

      doKPXTWebService(
        {
          CONTROL: {
            XFSH: "15000119780514458045",
            XFJH: "0",
            FQFIP: "127.0.0.1"
          },
          METHOD: "KJESCFP",
          METHODDATA: {
            DATAINFO: {
              FPZL: "42", // 发票种类，42 –二手车发票
              GFMC: buyerName, // 买方名称，字符72
              GFDM: buyerIdCard, // 买方单位代码/身份证号码，字符22
              GFDZ: buyerAddress, // 买方地址，字符80
              GFDH: buyerTelephone, // 买方电话，字符20
              XFMC: sellerName, // 卖方名称，字符72
              XFDM: sellerIdCard, // 卖方单位代码/身份证号码，字符22
              XFDZ: sellerAddress, // 卖方地址，字符80
              XFDH: sellerTelephone, // 卖方电话，字符20
              CPZH: vehicleNumber, // 车牌照号，字符20
              DJZH: vehicleCertificateCode, // 登记证号，字符20
              CLLX: vehicleType ? vehicleType : "", // 车辆类型，字符40
              CJH: vehicleIdentificationNumberCertificate, // 车架号/车辆识别代码，字符23
              CPXH: carLabelType ? carLabelType : "BH6430MY", // 厂牌型号，字符60
              // "CPXH": carLabelType, // 厂牌型号，字符60
              GOODSNOVER: "33.0", // 商品税收分类编码版本号 非空
              GOODSTAXNO: "109030602", // 商品税收分类编码—非空
              TAXPRE: "0", // 是否享受优惠政策，0：不享受，1：享受 二手车不交税，为0
              TAXPRECON: "", // 优惠政策 二手车不交税，为空
              ZERNOTAX: "", // 零税率标识 二手车不交税，为空
              CROPGOODSNO: "", // 企业自编码
              CGSMC: "江岸车管所", // 车管所名称
              HJJE: (vehiclePrice / 100).toString(), // 车价合计
              JYPMMC: "湖北航信", // 经营拍卖单位
              JYPMSH: "12345678901234567890", // 经营拍卖单位纳税人识别号
              JYPMYHZH: "6226123456789012346", // 经营拍卖单位开户银行，账号
              JYPMDZ: "武汉市洪山区中北路81号", // 经营拍卖单位地址
              JYPMDH: "027-5236215", // 经营拍卖单位电话
              SCMC: "湖北航信", // 二手车市场
              SCSH: "6226123456789012346", // 二手车市场纳税人识别号
              SCDZ: "", // 二手车市场 地址
              SCYHZH: "", // 二手车市场开户行,账号
              SCDH: "", // 二手车市场电话
              BZ: "二手车", // 备注
              KPR: "杨明", // 开票人
              SFXSQR: "0"
            }
          }
        },
        "KJESCFP"
      ).then(resp => {
        const { FPDM, FPHM, FHDM } = resp.DATAINFO;
        if (FHDM === "4011") {

          this.isContent = false;
          this.kaipiaoUrl = require('../assets/printDone0.png');
          this.isDybtn = false;
          this.isRepeatDybtn = false;

          const { kprq, fpdm, fphm, fhdm } = JSON.parse(
            JSON.stringify(resp.DATAINFO).toLocaleLowerCase()
          );
          let invoiceData = new FormData();
          invoiceData.append("kprq", kprq.split(" ")[0]);
          invoiceData.append("fpdm", fpdm);
          invoiceData.append("fphm", fphm);
          invoiceData.append("fpxx", fhdm === "4011" ? 1 : 0);
          invoiceData.append("id", invoiceId);
          postPretrialInfoSave(invoiceData).then(resp => {
            console.log(resp);
          });

          let invoiceOpData = new FormData();
          invoiceOpData.append("opContent", "");
          invoiceOpData.append("opStatus", 1);
          invoiceOpData.append("fpdm", fpdm);
          invoiceOpData.append("fphm", fphm);
          invoiceOpData.append("kprq", kprq.split(" ")[0]);
          invoiceOpData.append("fpxx", 1);
          invoiceOpData.append("id", flowId);
          getOpFlow(invoiceOpData).then(resp => {
            if (resp.status === 200) {
              Message({
                message: "开据发票成功!",
                type: "success",
                duration: 3 * 1000
              });
            }
          });
          this.$router.push({ path: "/blank" });
        } else {
          this.isDybtn = false;
          this.isContent = false;
          this.kaipiaoUrl = require('../assets/kaipiaoFailed2.png');
          this.isDybtn = false;
          this.isRepeatDybtn = true;
          Message({
            message: "出现故障，请重新打印",
            type: "error",
            duration: 3 * 1000,
            offset: 200
          });
        }
        setStorage("invoice", { FPDM: FPDM, FPHM: FPHM });
      }).catch(() => {
          console.log('Error');
          this.$router.push({path: '/exception'})
      })
    },
    /** 打印补打发票操作 */
    doDYFPwebservice() {
      const { FPDM, FPHM } = getStorage("invoice") ? getStorage("invoice") : ({"FPDM": "140110710000", "FPHM": "20000002"})
      doKPXTWebService(
        {
          CONTROL: {
            XFSH: "15000119780514458045", // 销方税号 字符20
            XFJH: "0", // 销方机号 数字3
            FQFIP: "127.0.0.1" // 操作提交方，发起方 IP
          },
          METHOD: "DYFP", // 打印发票
